import React, { useCallback } from 'react'
import { Link, Navigate } from 'react-router-dom'
import { useAppSelector, useTypedDispatch } from '../../redux/redux-store'
import { registerTC } from '../../redux/register-reducer'
import { useFormik } from 'formik'
import s from '../../styles/Authorization.module.css'
import {
  Button,
  FormControl,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
} from '@material-ui/core'
import { Visibility, VisibilityOff } from '@material-ui/icons'

type FormikErrorType = {
  email?: string
  password?: string
  confirmPassword?: string
}
type StatePassword = {
  password: string
  showPassword: boolean
}

type StateConfirmPassword = {
  confirmPassword: string
  showConfirmPassword: boolean
}

export const Registration = () => {
  const dispatch = useTypedDispatch()
  const isRegistered = useAppSelector((state) => state.register.isRegistered)

  const formik = useFormik({
    initialValues: {
      email: '',
      password: '',
      confirmPassword: '',
    },
    validate: (values) => {
      const errors: FormikErrorType = {}
      if (!values.email) {
        errors.email = 'Required'
      }
      if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
        errors.email = 'Invalid email address'
      }
      if (!values.password) {
        errors.password = 'Required'
      } else if (values.password.length <= 7) {
        errors.password = 'Password must be more than 7 characters...'
      }
      if (!values.confirmPassword) {
        errors.confirmPassword = 'Required'
      } else if (values.confirmPassword !== values.password) {
        errors.confirmPassword =
          'The password and confirmation password do not match'
      }
      return errors
    },
    onSubmit: (values) => {
      dispatch(registerTC(values))
      formik.resetForm()
    },
  })
  const [valuesPassword, setValuesPassword] = React.useState<StatePassword>({
    password: '',
    showPassword: false,
  })

  const [valuesConfirmPassword, setValuesConfirmPassword] =
    React.useState<StateConfirmPassword>({
      confirmPassword: '',
      showConfirmPassword: false,
    })

  const handleClickShowPassword = useCallback(() => {
    setValuesPassword({
      ...valuesPassword,
      showPassword: !valuesPassword.showPassword,
    })
  }, [valuesPassword])

  const handleClickShowConfirmPassword = useCallback(() => {
    setValuesConfirmPassword({
      ...valuesConfirmPassword,
      showConfirmPassword: !valuesConfirmPassword.showConfirmPassword,
    })
  }, [valuesConfirmPassword])

  const handleMouseDownPassword = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    event.preventDefault()
  }

  if (isRegistered) {
    return <Navigate to={'/login'} />
  }
  return (
    <div className={s.container}>
            <form onSubmit={formik.handleSubmit} className={s.form}>
            <h3>Register</h3>
        <FormControl>
          <InputLabel color="primary">Email</InputLabel>
          <Input
            id="email"
            type="email"
            placeholder={'Email'}
            className={s.input}
            color="primary"
            {...formik.getFieldProps('email')}
          />
        </FormControl>
        {formik.touched.email && formik.errors.email ? (
          <div>{formik.errors.email}</div>
        ) : null}
        <FormControl>
          <InputLabel color="primary">Password</InputLabel>
          <Input
            id="password"
            type={valuesPassword.showPassword ? 'text' : 'password'}
            placeholder={'Password'}
            className={s.input}
            color="primary"
            {...formik.getFieldProps('password')}
            autoComplete="on"
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}>
                  {valuesPassword.showPassword ? (
                    <VisibilityOff />
                  ) : (
                    <Visibility />
                  )}
                </IconButton>
              </InputAdornment>
            }
          />
        </FormControl>

        {formik.touched.password && formik.errors.password ? (
          <div>{formik.errors.password}</div>
        ) : null}
          <FormControl>
                    <InputLabel color={'primary'}>Confirm password</InputLabel>
                    <Input
                        id="confirmPassword"
                        type={valuesConfirmPassword.showConfirmPassword ? 'text' : 'password'}
                        placeholder={'Confirm password'}
                        className={s.input}
                        color={'primary'}
                        {...formik.getFieldProps('confirmPassword')}
                        autoComplete="on"
                        endAdornment={
                            <InputAdornment position="end">
                                <IconButton
                                    onClick={handleClickShowConfirmPassword}
                                    onMouseDown={handleMouseDownPassword}
                                >
                                    {valuesConfirmPassword.showConfirmPassword ? <VisibilityOff/> : <Visibility/>}
                                </IconButton>
                            </InputAdornment>
                        }
                    />
                </FormControl>

        {formik.touched.confirmPassword && formik.errors.confirmPassword ? (
          <div>{formik.errors.confirmPassword}</div>
        ) : null}

        <Button type={'submit'} variant={'contained'} color={'primary'}>
          Register
        </Button>
        <div className={s.text}>Already have an account?</div>
        <Link to={'/login'}>Sign In</Link>
      </form>
    </div>
  )
}
